<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">缓存管理</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td style="width:100px">{{item.type}}</td>
                    <td>{{item.name}}</td>
                    <td style="width:100px;">
                        <Button class="spanBox" >清除</Button>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :total="100" show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    let params = new FormData();
  },
  data() {
    return {
      // ==============弹框二
      roleSelectShow: false, //判定弹框下拉框是否显示
      formItem: {
        select: "",
        radio: "male"
      },
      tableHeader: [
        { name: "类别" },
        { name: "名称" },
        { name: "操作" },
      ],
      tableBody: [
        {
          id: "12",
          type: "eng_1",
          name: "系统管理员",
        },
        {
          id: "13",
          type: "eng_2",
          name: "系统管理员",
        }
      ],
    };
  },
  methods: {
  }
};
</script>
<style scoped>
</style>



